@-webkit-keyframes loading {
from {
                opacity: 1;
                -webkit-transform: translateY(0);
                        transform: translateY(0);
}
to {
                opacity: 0.1;
                -webkit-transform: translateY(-2rem);
                        transform: translateY(-2rem);
}
}
@keyframes loading {
from {
                opacity: 1;
                -webkit-transform: translateY(0);
                        transform: translateY(0);
}
to {
                opacity: 0.1;
                -webkit-transform: translateY(-2rem);
                        transform: translateY(-2rem);
}
}
.loading {
            display: -webkit-box;
            display: -ms-flexbox;
            display: flex;
				   /* -ms-flex-pack: center;*/
				    -webkit-box-pack: center;
				        -ms-flex-pack: center;
				            justify-content: center;
				    height: 100px;
				    background: #000;
				    /*-ms-flex-align: center;*/
				    -webkit-box-align: center;
				        -ms-flex-align: center;
				            align-items: center;
				    width: 100px;
				    padding-top: 28px;
				    -webkit-box-sizing: border-box;
				            box-sizing: border-box;
				    border-radius: 12px;
				    position: fixed;
				    top: 35%;
				    left: 50%;
				    margin-left: -50px;
				    opacity: 0.8;
}
.loading>div {
            width: 20px;
				    height: 20px;
				    border-radius: 50%;
				    background: #fff;
				    opacity: 0.2;
				    margin: 5px;
				    /* margin-bottom: 0; */
				    color: #fff;
				    text-align: center;
				    line-height: 20px;
				    font-size: 1.5rem;
				    -webkit-animation: loading .8s infinite alternate;
				            animation: loading .8s infinite alternate;
}
.loading>div:nth-child(2) {
            -webkit-animation-delay: 0.4s;
                    animation-delay: 0.4s
}
.loading>div:nth-child(3) {
            -webkit-animation-delay: 0.6s;
                    animation-delay: 0.6s;
}


